import {mapState} from 'vuex';
export default{
	data(){
		return {
			showMusic:false
		}
	},
	computed:{
		...mapState({
			cur_music_msg:'cur_music_msg',
			isPlay:'isPlay'
		}),
	},
	methods:{
		showPlaying(){
			uni.navigateTo({
				 url: '/pages/playing/playing'
			})
		}
	},
	watch:{
		cur_music_msg(newVal){
			if(newVal != null){
				this.showMusic = true
			}else{
				this.showMusic = false
			}
		}
	}
}
